import React, { PureComponent } from "react";
import { connect } from 'react-redux';
import { filterPostDataAppendWebFilter } from '../../../../actions/themeActions'
import FilterNetworks from "./Elements/FilterNetworks";
class RoundEdgedFilter extends PureComponent {
  state = { networkID: null }
  onClickWebFilters = (networkID) => event => {
    this.props.changeFilterStatus(2)
    const tstamp = Math.floor(Date.now() / 1000);
    const { appendData, wall } = this.props;
    let postCount = wall.ThemeRule.numberOfPosts;
    this.props.filterPostDataAppendWebFilter(
      wall.Wall.id,
      tstamp,
      postCount,
      networkID,
      appendData.after,
      appendData.heightEvent,
      null
    );
    this.setState({ networkID: networkID })
  }


  render() {
    const { webFilters, languageSetting } = this.props;
    const { networkID } = this.state;
    return (
      <div className="tb_filter_round_edged_wrapper">
        <div className="tb_filter_round_edged_list" onClick={this.onClickWebFilters(0)}>
          <div className={`tb_filter_round_edged_button_text ${(networkID === 0) && this.props.webFiltersStatus == 2 ? `tb_filter_active` : ``}`}>{languageSetting.filterButton}</div>
        </div>
        {
          (webFilters && webFilters.length > 0) ? webFilters.map((item) => {

            return <div key={item.id} className={`tb_filter_round_edged_list`} onClick={this.onClickWebFilters(item.Network.id)}>
              <div className={`tb_filter_round_edged_button${(networkID && networkID == item.Network.id) && this.props.webFiltersStatus == 2 ? ` tb_filter_active` : ``}`}>
                <FilterNetworks filterIconClass={'tb_filter_round_edge_ico'} network={item.Network} isDefault={false} />
              </div>
            </div>
          }) : null
        }
      </div>
    );
  }
}


const mapStateToProps = state => {
  return {
  }
}
const mapDispatchToProps = dispatch => {
  return {
    filterPostDataAppendWebFilter: (wallID, timeStamp, postCount, networkId, after, heightEvent, tagName) => dispatch(filterPostDataAppendWebFilter(wallID, timeStamp, postCount, networkId, after, heightEvent, tagName))
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(RoundEdgedFilter);